(function () {

//////////////////////////////////////////////////////////////////////////////////
//                                                                              //
// packages/jagi:astronomy-behaviors/lib/module/global.js                       //
//                                                                              //
//////////////////////////////////////////////////////////////////////////////////
                                                                                //
Astro.behaviors = {};                                                           // 1
                                                                                // 2
//////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

//////////////////////////////////////////////////////////////////////////////////
//                                                                              //
// packages/jagi:astronomy-behaviors/lib/module/utils.js                        //
//                                                                              //
//////////////////////////////////////////////////////////////////////////////////
                                                                                //
Astro.utils.behaviors = {                                                       // 1
  findBehavior: function(Class, behaviorName) {                                 // 2
    return Astro.utils.class.findInClass(Class, function(Class) {               // 3
      return Class.getBehavior(behaviorName);                                   // 4
    });                                                                         // 5
  }                                                                             // 6
};                                                                              // 7
                                                                                // 8
//////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

//////////////////////////////////////////////////////////////////////////////////
//                                                                              //
// packages/jagi:astronomy-behaviors/lib/module/behavior.js                     //
//                                                                              //
//////////////////////////////////////////////////////////////////////////////////
                                                                                //
var checks = {};                                                                // 1
                                                                                // 2
checks.behaviorDefinition = function(behaviorDefinition) {                      // 3
  // Check parameters validity.                                                 // 4
  if (!_.isObject(behaviorDefinition)) {                                        // 5
    throw new Error('The behavior definition has to be an object');             // 6
  }                                                                             // 7
                                                                                // 8
  // Check if behavior name is provided.                                        // 9
  if (!_.has(behaviorDefinition, 'name')) {                                     // 10
    throw new Error('The behavior name can not be empty');                      // 11
  }                                                                             // 12
                                                                                // 13
  // Check if behavior name is a string.                                        // 14
  if (!_.isString(behaviorDefinition.name)) {                                   // 15
    throw new Error('The behavior name has to be a string');                    // 16
  }                                                                             // 17
                                                                                // 18
  // Check if behavior with given name already exists.                          // 19
  if (_.has(Astro.behaviors, behaviorDefinition.name)) {                        // 20
    throw new Error(                                                            // 21
      'The behavior with the name "' +                                          // 22
      behaviorDefinition.name + '" is already defined'                          // 23
    );                                                                          // 24
  }                                                                             // 25
};                                                                              // 26
                                                                                // 27
Astro.createBehavior = function(behaviorDefinition) {                           // 28
  // Check validity of the behavior definition.                                 // 29
  checks.behaviorDefinition(behaviorDefinition);                                // 30
                                                                                // 31
  // Initialize a module if the "init" method had been defined.                 // 32
  if (_.isFunction(behaviorDefinition.init)) {                                  // 33
    behaviorDefinition.init();                                                  // 34
  }                                                                             // 35
                                                                                // 36
  // Add the behavior definition to the global list of behaviors.               // 37
  return Astro.behaviors[behaviorDefinition.name] = behaviorDefinition;         // 38
};                                                                              // 39
                                                                                // 40
//////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

//////////////////////////////////////////////////////////////////////////////////
//                                                                              //
// packages/jagi:astronomy-behaviors/lib/module/init_class.js                   //
//                                                                              //
//////////////////////////////////////////////////////////////////////////////////
                                                                                //
var checks = {};                                                                // 1
                                                                                // 2
checks.behaviorName = function(behaviorName) {                                  // 3
  if (!_.isString(behaviorName)) {                                              // 4
    throw new Error(                                                            // 5
      'The behavior name in the "' + this.getName() +                           // 6
      '" class schema has to be a string'                                       // 7
    );                                                                          // 8
  }                                                                             // 9
};                                                                              // 10
                                                                                // 11
checks.exists = function(behaviorName) {                                        // 12
  if (!_.has(Astro.behaviors, behaviorName)) {                                  // 13
    throw new Error(                                                            // 14
      'The "' + behaviorName + '" behavior in "' + this.getName() +             // 15
      '" class schema does not exist'                                           // 16
    );                                                                          // 17
  }                                                                             // 18
};                                                                              // 19
                                                                                // 20
checks.behaviorData = function(behaviorName, behaviorData) {                    // 21
  if (!_.isObject(behaviorData)) {                                              // 22
    throw new Error(                                                            // 23
      'The behavior data in "' + this.getName() +                               // 24
      '" class schema has to be an object'                                      // 25
    );                                                                          // 26
  }                                                                             // 27
};                                                                              // 28
                                                                                // 29
checks.added = function(behaviorName) {                                         // 30
  if (Astro.utils.behaviors.findBehavior(this, behaviorName)) {                 // 31
    throw new Error(                                                            // 32
      'The "' + behaviorName + '" behavior in the "' +                          // 33
      this.getName() + '" class schema had already been added'                  // 34
    );                                                                          // 35
  }                                                                             // 36
};                                                                              // 37
                                                                                // 38
var methods = {};                                                               // 39
                                                                                // 40
methods.hasBehavior = function(behaviorName) {                                  // 41
  // Check if the behavior name has been provided                               // 42
  checks.behaviorName.call(this, behaviorName);                                 // 43
                                                                                // 44
  return _.has(this.schema.behaviors, behaviorName);                            // 45
};                                                                              // 46
                                                                                // 47
methods.getBehavior = function(behaviorName) {                                  // 48
  // Check if the behavior name has been provided                               // 49
  checks.behaviorName.call(this, behaviorName);                                 // 50
                                                                                // 51
  return this.schema.behaviors[behaviorName];                                   // 52
};                                                                              // 53
                                                                                // 54
methods.getBehaviors = function() {                                             // 55
  return this.schema.behaviors;                                                 // 56
};                                                                              // 57
                                                                                // 58
methods.addBehavior = function(behaviorName, behaviorData) {                    // 59
  behaviorData = behaviorData || {};                                            // 60
                                                                                // 61
  // Check if the behavior name has been provided                               // 62
  checks.behaviorName.call(this, behaviorName);                                 // 63
  // Check if the given bahavior exists.                                        // 64
  checks.exists.call(this, behaviorName);                                       // 65
  // Check if the behavior data is an object.                                   // 66
  checks.behaviorData.call(this, behaviorName, behaviorData);                   // 67
  // Check if the given behavior is already added to this or parent classes.    // 68
  checks.added.call(this, behaviorName);                                        // 69
                                                                                // 70
  // Add behavior copy to behaviors list of given schema under the key equal    // 71
  // to behavior type.                                                          // 72
  this.schema.behaviors[behaviorName] = behaviorData;                           // 73
                                                                                // 74
  // Get behavior definition.                                                   // 75
  var behavior = Astro.behaviors[behaviorName];                                 // 76
  // Trigger "addbehavior" event.                                               // 77
  if (behavior.events && behavior.events.addbehavior) {                         // 78
    behavior.events.addbehavior.call(this, behaviorData);                       // 79
  }                                                                             // 80
};                                                                              // 81
                                                                                // 82
methods.addBehaviors = function(behaviors) {                                    // 83
  if (_.isArray(behaviors)) {                                                   // 84
                                                                                // 85
    _.each(behaviors, function(behaviorName) {                                  // 86
      this.addBehavior(behaviorName);                                           // 87
    }, this);                                                                   // 88
                                                                                // 89
  } else if (_.isObject(behaviors)) {                                           // 90
                                                                                // 91
    _.each(behaviors, function(behavior, behaviorName) {                        // 92
      this.addBehavior(behaviorName, behavior);                                 // 93
    }, this);                                                                   // 94
                                                                                // 95
  } else {                                                                      // 96
                                                                                // 97
    // Behaviors data has to be an object or an array.                          // 98
    throw new Error(                                                            // 99
      'The behavior data in the "' + this.getName() +                           // 100
      '" class schema has to ' +                                                // 101
      'be an object or an array'                                                // 102
    );                                                                          // 103
                                                                                // 104
  }                                                                             // 105
};                                                                              // 106
                                                                                // 107
onInitClass = function(schemaDefinition) {                                      // 108
  var Class = this;                                                             // 109
                                                                                // 110
  _.extend(Class, methods);                                                     // 111
                                                                                // 112
  // Add the "behaviors" attribute to the schema.                               // 113
  Class.schema.behaviors = {};                                                  // 114
                                                                                // 115
  if (_.has(schemaDefinition, 'behaviors')) {                                   // 116
    Class.addBehaviors(schemaDefinition.behaviors);                             // 117
                                                                                // 118
    // Trigger on "initclass" event if it is provided. We do it here because we // 119
    // are already in the process of execution "initclass" events, so adding it // 120
    // to class would have no effect.                                           // 121
    _.each(Class.getBehaviors(), function(behaviorData, behaviorName) {         // 122
      var behavior = Astro.behaviors[behaviorName];                             // 123
      if (behavior.events && behavior.events.initclass) {                       // 124
        behavior.events.initclass.call(Class, schemaDefinition);                // 125
      }                                                                         // 126
    });                                                                         // 127
  }                                                                             // 128
};                                                                              // 129
                                                                                // 130
//////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

//////////////////////////////////////////////////////////////////////////////////
//                                                                              //
// packages/jagi:astronomy-behaviors/lib/module/init_instance.js                //
//                                                                              //
//////////////////////////////////////////////////////////////////////////////////
                                                                                //
onInitInstance = function(attrs) {                                              // 1
  var Class = this.constructor;                                                 // 2
  var behaviors = Class.getBehaviors();                                         // 3
                                                                                // 4
  _.each(behaviors, function(behaviorData, behaviorName) {                      // 5
    var behavior = Astro.behaviors[behaviorName];                               // 6
    if (behavior.events && behavior.events.initinstance) {                      // 7
      behavior.events.initinstance.call(Class, attrs);                          // 8
    }                                                                           // 9
  });                                                                           // 10
};                                                                              // 11
                                                                                // 12
//////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

//////////////////////////////////////////////////////////////////////////////////
//                                                                              //
// packages/jagi:astronomy-behaviors/lib/module/module.js                       //
//                                                                              //
//////////////////////////////////////////////////////////////////////////////////
                                                                                //
Astro.createModule({                                                            // 1
  name: 'behaviors',                                                            // 2
  events: {                                                                     // 3
    initclass: onInitClass,                                                     // 4
    initinstance: onInitInstance                                                // 5
  }                                                                             // 6
});                                                                             // 7
                                                                                // 8
//////////////////////////////////////////////////////////////////////////////////

}).call(this);
